import React, { Component } from 'react';
import './App.css';
import Artists from './Artists';
import Tracks from './Tracks';


// var apiKey = "ed5da9b66fbbb3d6781c2dec474a1eb9"

class App extends Component {

  constructor(props){
    super(props);
    this.state = {
      url: "http://ws.audioscrobbler.com/2.0/", 
      topArtists:[], 
      artistsGrabbed: false, 
      topTracks: [],
      tracksGrabbed: false,
      currentList: "artists"
    };
  }


  render() {
    let self = this;
    console.log(self.state.topArtists)
    console.log(self.state.topTracks)
    // console.log(self.state.topArtists.length)

    if (self.state.topArtists.length === undefined){
      self.state.artistsGrabbed = true;
      // console.log("hi" + JSON.stringify(self.state.topArtists.artists) )
      // console.log("hi " + JSON.stringify(self.state.topArtists.artists.artist) )

      // this.state.topArtists.artists.artist.map((anArtist) =>
      //     console.log(anArtist.name))
  
    }

    if (self.state.topTracks.length === undefined){
      self.state.tracksGrabbed = true;
  
    }

    console.log(self.state.artistsGrabbed)
    return (
      <div id = "main-container">
        <p id = "heading" class = "row justify-content-center">Last.fm Music Charts</p>
        <div id = "buttons-div" class = "row justify-content-center"> 
          <button className = "toggle-button col-md-2 col-4" id = "artist-toggle">Top Artists</button>
          <button className = "toggle-button col-md-2 col-4" id = "track-toggle">Top Tracks</button> 
        </div>
        <div id = "list" className = "row justify-content-center">
          {self.state.artistsGrabbed
          ? this.state.topArtists.artists.artist.map((anArtist, index) =>
            <Artists 
            key={anArtist.mbid}
            id={anArtist.mbid}
            artistName={anArtist.name}
            artistPlayCount={anArtist.playcount}
            artistRanking={index+1}
            />) //     favorite={this.favoriteArtist}
          : <p>Top artists data being fetched.</p>
          }
        </div>

        {self.state.tracksGrabbed
        ? this.state.topTracks.tracks.track.map((aTrack) =>
          <Tracks 
          key={aTrack.name}
          id={aTrack.name}
          trackName={aTrack.name}
          trackPlayCount={aTrack.playcount}
          />) //     favorite={this.favoriteArtist}
        : <p>Top tracks data being fetched.</p>
        }
 
      </div>
    );
  }

  componentDidMount() {
    // Simple GET request using fetch
    fetch('https://ws.audioscrobbler.com/2.0/?method=chart.gettopartists&api_key=ed5da9b66fbbb3d6781c2dec474a1eb9&format=json&limit=10')
      .then(response => response.json())
      .then(data => this.setState({ topArtists: data}));// this.setState({ totalReactPackages: data.total })); {console.log(data) }
    
      fetch('https://ws.audioscrobbler.com/2.0/?method=chart.gettoptracks&api_key=ed5da9b66fbbb3d6781c2dec474a1eb9&format=json&limit=10')
      .then(response => response.json())
      .then(data => this.setState({ topTracks: data}));
  }

  renderList(currentList){
    if (currentList == "artists"){

    }
    else if(currentList == "tracks"){

    }
    else{
      return ("an error occured. there is no list selected currently.")
    }
  }

}
export default App;


